
.weather-icon {
  position: fixed;
  top: 25px;
  right: 15px;

  background-color: #ff8383;
  background-blend-mode: exclusion;

  padding: 5px;
  border-radius: 10px;
  max-width: 80px;

  cursor: pointer;
  opacity: 0.9;
  filter: blur(2px); 

  text-align: center;
}

.weather-icon:hover {
  opacity: 1;
  filter: none;
}

.weather-icon img {
  background-blend-mode: exclusion;
  scale: 0.1;
}

.weather-icon p {
  font-size: 10px;
  color: white;
  position: relative; 
  font-family: "Roboto", sans-serif;  
  text-align: center; 
  mix-blend-mode: difference;
  user-select: none;
  /* font-weight: black; */
  text-transform: uppercase;
}